.tabs{
  &-price{
    &__content{
      margin-top: 30px;
    }
  }
}

.order{
  &-steps{
    &__item + &__item{
      margin-top: 40px;
    }
  }
  &-step{
    &__title{
      margin-bottom: 40px;
    }
    &__block + &__block{
      margin-top: 30px;
    }

    &__block{
      max-width: 530px;
      width: 100%;

      display: flex;
      align-items: center;

      position: relative;
    }

    &__sort{
      display: flex;
      justify-content: flex-end;

      width: 100%;
    }
  }
  &-block + &-block{
    margin-top: 20px;
  }
  &-block{
    &__title{
      font-size: 16px;
      line-height: 22px;
      letter-spacing: -0.3px;

      color: #297BC7;
    }
    &__content{
      margin-top: 12px;
    }
  }
  &-step{
    &__title{
      font-size: 30px;
      font-weight: 500;

      color:#fff;
    }
  }
}

.operation{
  &-radio{
    &__wrapper{
      display: flex;
      width: max-content;
      align-items: center;

      padding: 4px;
      background: #0A2545;
      border-radius: 10px;
    }
  }
  &__radio{
    &-input{
      display: none;
      &:checked ~ .operation__radio-checkmark{
        color:#fff;
        background: #14386E;
      }
    }
    &-checkmark{
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100px;
      height: 40px;
      font-weight: 500;
      font-size: 18px;
      letter-spacing: -0.3px;

      color: #329DFF;

      border-radius: 8px;


      transition: .3s all linear;
    }
  }
  &__input{
    width: 100%;
    max-width: 530px;
    display: flex;
    align-items: center;
    padding: 16px;
    height: 70px;
    background: #14386E;
    border-radius: 14px;

    position: relative;
    &-remove{
      width: 20px;
      height: 20px;
      position: absolute;
      top: 50%;
      margin-top: -10px;
      right: -35px;
      svg{
        width: 100%;
        height: 100%;
      }
    }
  }
  &__select{
    width: 100%;
    height: 100%;

    background:none;
    outline: none;
    border: none;

    font-size: 22px;
    font-weight: 500;
    color:#fff;
  }
  &__link{
    display: flex;
    align-items: center;
    width: 100%;
    &::after{
      content: '';
      width: 15px;
      height: 15px;
      display: block;
      background: url("../img/white-arrow.svg") center center no-repeat;
      background-size: contain;

      margin-left: auto;
    }
    &-img{
      width: 40px;
      height: 40px;
      img{
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
    &-label{
      margin-left: 20px;
      font-size: 22px;
      color:#fff;
      span{
        font-weight: 700;
      }
    }
  }
}

.count{
  &__wrapper{
    display: flex;
    align-items: center;

    .count-block + .count-block{
      margin-left: 40px;
    }
  }
}

.count-block{
  display: flex;
  align-items: center;
  &_small{
    .count-block__wrapper{
      width: 150px;
    }
  }
  &_medium{
    .count-block__wrapper{
      width: 240px;
    }
  }
  &__wrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;

    padding: 15px 20px;

    background: #0A2545;
    border-radius: 14px;
  }

  &__minus{
    background: url("../img/minus.svg") center center no-repeat;
    background-size: contain;
    margin-right: 23px;
  }

  &__plus{
    background: url("../img/plus.svg") center center no-repeat;
    background-size: contain;
    margin-left: 23px;
  }

  &__minus,&__plus{
    width: 24px;
    height: 24px;

    flex-shrink: 0;
  }

  &__count{
    input{
      width: 100%;

      text-align: center;

      color:#Fff;
      font-size: 18px;
      font-weight: 500;
      letter-spacing: -0.3px;

      background: none;
      outline: none;
      border: none;
    }
  }

  &__desc{
    font-size: 18px;
    color:#fff;

    letter-spacing: -0.3px;
  }

  &__wrapper + &__desc{
    margin-left: 22px;
  }

  &__desc + &__wrapper{
    margin-left: 22px;
  }
}

.custom-select{
  display: flex;
  align-items: center;
  &__label{
    font-size: 18px;
    letter-spacing: -0.3px;

    color: #FFFFFF;

    margin-right: 20px;
  }
  select{
    background: none;
    outline: none;
    border: none;

    letter-spacing: -0.3px;

    color: #329DFF;
    font-size: 18px;
  }
  &_rever{
    .custom-select{
      &__label{
        color:#329DFF;
      }

      select{
        color:#fff;
      }
    }
  }
}

.step{
  &__btn{
    margin: 70px auto 0 auto;
    max-width: 530px;
    width: 100%;
  }
}

.activity-grid{
  display: flex;
  align-items: center;
  & + &{
    margin-top: 25px;
  }
  &__item + &__item{
    margin-left: 100px;
  }
}

.day-list{
  display: flex;
  align-items: center;
  &__item + &__item{
    margin-left: 20px;
  }
  &__item{
    font-weight: 500;
    font-size: 18px;
    color:#FFFFFF;

    letter-spacing: -.3px;
  }
}

.activity-block{
  & + &{
    margin-top: 25px;
    padding-top: 40px;

    border-top: 1px solid  #1E5BA4;
  }
  &__radio{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-text{
      font-weight: 500;
      color:#fff;
      font-size: 18px;
      letter-spacing: -0.3px;
      margin-right: 15px;
    }
  }
  &__item + &__item{
    margin-top: 30px;
  }
  &__checkbox{
    .checkbox{
      &__input{
        &:checked ~ .checkbox__content{
          color:#fff;
        }
      }
    }
  }
}

.requisites-block{
  & + &{
    margin-top: 40px;
  }
  &__radio{
    display: flex;
    align-items: center;
    width: max-content;
    &-text{
      font-weight: 500;
      color:#fff;
      font-size: 18px;
      letter-spacing: -0.3px;
      margin-right: 50px;
    }
  }
  &__header{
    margin-bottom: 25px;
  }
  &__footer{
    margin-top: 40px;
  }
  &__logo{
    display: flex;
    align-items: center;

    margin-bottom: 50px;
    img{
      width: 40px;
      height: 40px;

      object-fit: contain;
    }
    &-text{
      margin-left: 15px;
      font-weight: 500;
      font-size: 22px;

      color: #FFFFFF;
    }
  }
}

.requisites-text{
  background: #0A2545;
  border-radius: 14px;

  padding: 25px 25px 60px 25px;

  font-size: 18px;
  line-height: 24px;
  /* or 133% */

  letter-spacing: -0.3px;

  color: #297BC7;

  p + p{
    margin-top: 30px;
  }
}

@media screen and (max-width: 1100px) {
  .count{
    &__wrapper{
      flex-direction: column;
      align-items: flex-start;
      .count-block + .count-block{
        margin-left: 0;

        margin-top: 20px;
      }
    }
  }
}

@media screen and (max-width: 700px) {
  .day-list{
    flex-wrap: wrap;
    &__item + &__item{
      margin-left: 10px;
    }
    &__item{
      margin: 10px;
      font-size: 16px;
    }
  }
  .activity-grid{
    &__item + &__item{
      margin-left: 10px;
    }
  }

  .operation{
    &__input{
      &-remove{
        position: relative;
        top: 0;
        margin-top: 0;
        right: 0;
        margin-left: 15px;

        display: inline-flex;
      }
    }
  }
}

@media screen and (max-width: 475px){
  .count{
    &-block{
      &_medium{
        .count-block__wrapper{
          width: 200px;
        }
      }
    }
  }
  .order{
    &-step{
      &__title{
        font-size: 24px;
      }
    }
  }
}