.settings{
  &-grid{
    display: flex;
    align-items: flex-start;
    justify-content: center;
    &__item + &__item{
      margin-left: 30px;
    }
    &__item{
      max-width: 490px;
      width: 100%;
      flex-shrink: 0;
      &_big{
        max-width: 690px;
      }
    }
  }
  &-tabs{
    &__content{
      margin-top: 40px;
    }
  }
  &-content{
    &__header{
      margin-bottom: 40px;
    }
  }
  &-wrapper{
    &__item{
      padding: 50px 30px 66px 30px;

      border: 2px solid #1E5BA4;
      border-radius: 16px;
    }

    &__title{
      display: flex;
      align-items: center;
      justify-content: space-between;
      &-label{
        font-size: 30px;
        font-weight: 500;

        line-height: 1.16;

        color:#fff;
      }
    }

    &__chat{
      margin-top: 40px;
    }

    &__item + &__item{
      margin-top: 30px;
    }

    &__content{
      max-width: 1010px;
      width: 100%;
      margin: 0 auto;
    }

    &__btn{
      height: 60px;
      margin-top: 60px;
    }
  }
  &-list{
    display: flex;
    align-items: center;
    &__item + &__item{
      margin-left: 40px;
    }
    &__item{
      display: flex;
      align-items: center;
      &_current{
        .settings-list__link{
          color: #fff;
        }
      }
    }
    &__link{
      font-weight: 500;
      font-size: 22px;
      letter-spacing: -0.3px;

      color: #329DFF;
      & + .notification-counter{
        margin-left: 12px;
      }
    }
  }
  &__header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-block{
      display: flex;
      align-items: center;
    }
    &-btn{
      width: 170px;
      font-size: 18px;
      margin-left: 60px;
    }
  }
}

.new-link{
  &__label{
    font-size: 18px;
    letter-spacing: -0.3px;

    color: #329DFF;
  }
  &__wrapper{
    display: flex;
    align-items: center;
  }
  &__icon{
    width: 18px;
    height: 18px;
    display: block;
    margin-left: 15px;

    svg{
      width: 100%;
      height: 100%;
    }
  }
}

@media screen and (max-width: 475px) {
  .settings{
    &-list{
      &__item + &__item{
        margin-left: 20px;
      }
      &__link{
        font-size: 18px;
      }
    }
    &-wrapper{
      &__title{
        &-label{
          font-size: 22px;
        }
      }
      &__item{
        padding: 30px 15px;
      }
    }
    &__header{
      &-btn{
        width: 150px;
        height: 50px;
        font-size: 16px;
      }
    }
  }
  .page-block{
    &__title{
      font-size: 30px;
    }
  }
  .sort-item{
    &__label{
      font-size: 14px;
    }
  }
}

@media screen and (max-width: 700px) {
  .settings{
    &__header{
      position: relative;
      align-items: flex-end;
      .page-block__title{
        position: absolute;
        left: 0;
        top: 0;
      }
      &-block{
        flex-wrap: wrap;
        flex-direction: column-reverse;
        align-items: flex-end;
        width: 100%;
      }
      &-select{
        margin-top: 30px;
        align-self: flex-start;
      }
    }
  }
}