.tabs{
  &-nav{
    display: flex;
    align-items: center;
    width: fit-content;
    padding: 4px;

    background: #0A2545;
    border-radius: 10px;
    &__link{
      display: block;
      padding: 8px 20px;
      border-radius: 8px;

      font-size: 18px;
      letter-spacing: -0.3px;
      font-weight: 500;
      color: #329DFF;

      transition: .3s all linear;
      &.active{
        background: #14386E;
        color:#fff;
      }
    }
  }
  &-content{
    &__item{
      display: none;
      &.active{
        display: block;
      }
    }
  }
  &-header{
    display: flex;
    align-items: center;
    &__content{
      margin-left: 40px;
    }
  }
  &-price{
    &__nav{
      &-item{
        .tabs-nav__link{
          width: 260px;
          text-align: center;
        }
      }
    }
  }
}

@media screen and (max-width: 700px) {
  .tabs{
    &-price{
      &__nav{
        &-item{
          .tabs-nav__link{
            width: auto;
            text-align: left;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 475px) {
  .tabs{
    &-price{
      &__nav{
        &-item{
          .tabs-nav__link{
            font-size: 12px;
          }
        }
      }
    }
    &-nav{
      &__link{
        font-size: 14px;
      }
    }
  }
}